/* eslint-disable @typescript-eslint/no-explicit-any */

import * as React from 'react';
import { useEffect, useState, useCallback, useMemo } from 'react';
import { Link } from 'gatsby';
import { throttle } from 'lodash';
import { FontAwesomeIcon as Fa } from '@fortawesome/react-fontawesome';
import { faTags,  faRandom , faMoon, faSun, faChevronRight, faUserCircle } from '@fortawesome/free-solid-svg-icons';

import './randomPostList.scss';

interface PostListProps {
  posts: any[];
}

const RandomPostList = (props: PostListProps) => {
  const { posts } = props;
  const [showCnt, setShowCnt] = useState(10);
  const [currentPostList, setCurrentPostList] = useState<JSX.Element[]>([]);
  const randomPosts = useMemo(() => {
    let shuffled = posts
      .map((value) => ({ value, sort: Math.random() }))
      .sort((a, b) => a.sort - b.sort)
      .map(({ value }) => value)

    return shuffled;

  }, [posts]);

  const throttleScrollHandler = useCallback(
    throttle(() => {
      if (
        window.outerHeight > (document.querySelector('.post-list') as HTMLDivElement).getBoundingClientRect().bottom
      ) {
        setShowCnt((prev: number) => {
          if (prev >= randomPosts.length) return prev;
          return prev + 10;
        });
      }
    }, 250),
    [randomPosts]
  );

  const handleShuffle = () => {
    let shuffled = posts
      .map((value) => ({ value, sort: Math.random() }))
      .sort((a, b) => a.sort - b.sort)
      .map(({ value }) => value)

    setCurrentPostList([])
    expendPostList(shuffled.slice(0, 3))


  };

  const expendPostList = useCallback((list: any) => {
    const mapToList = list.map((post: any) => {
      const { node } = post;
      const { excerpt, fields, frontmatter } = node;
      const { slug } = fields;
      const { date, title, tags } = frontmatter;
      let update = frontmatter.update;
      if (Number(update.split(',')[1]) === 1) update = null;

      const mapTag = tags.map((tag: string) => {
        if (tag === 'undefined') return;

        return (
          <div key={`${slug}-${tag}`} className="tag">
            <span>
              <Link to={`/tags#${tag}`}>{`#${tag}`}</Link>
            </span>
          </div>
        );
      });

      return (
        <li key={slug} className={`post`}>
          <article>
            <h2 className="title">
              <Link to={slug}>{title}</Link>
            </h2>
            <div className="info">
              <div className="date-wrap">
                <span className="date">{date}</span>
                {update ? <span className="update">&nbsp;{`(Updated: ${update})`}</span> : null}
              </div>
              {tags.length && tags[0] !== 'undefined' ? <span className="info-dot">Â·</span> : null}
              <ul className="tag-list">{mapTag}</ul>
            </div>
            <Link to={slug}>
              <span className="excerpt">{excerpt}</span>
            </Link>
          </article>
        </li>
      );
    });

    setCurrentPostList((prev: JSX.Element[]) => {
      return [...prev, ...mapToList];
    });
  }, []);

  useEffect(() => {
    if (showCnt > 0 && showCnt !== 10) expendPostList(randomPosts.slice(currentPostList.length, showCnt));
  }, [showCnt]);

  useEffect(() => {
    if (currentPostList.length) setCurrentPostList([]);

    setShowCnt((prev: number) => {
      if (prev === 10) expendPostList(randomPosts.slice(0, 10));
      return 10;
    });

    window.addEventListener('scroll', throttleScrollHandler);

    return () => {
      window.removeEventListener('scroll', throttleScrollHandler);
    };
  }, [randomPosts]);

  return (
    <div className="random-post-list">
      <ul>
        <li  className={`post`}>
          <article>
            <button onClick={handleShuffle}>
              <Fa icon={faRandom} />
            </button>
          </article>
        </li>
        {currentPostList}
      </ul>
    </div>
  );
};

export default RandomPostList;
